import waitPay from './waitPay.vue'
import waitEvaluate from './waitEvaluate.vue'
import over from './over.vue'
import waitSend from './waitSend.vue'
import waitReceive from './waitReceive.vue'
export default {
	data(){
		return {
			items:["全部","待付款","待发货","待收货","待评价"],
			arr:[
				{waitPayShow:true},
				{waitEvaluateShow:true},
				{waitSendShow:true},
				{waitReceiveShow:true},
				{overShow:true}
			],
			aaa:true,
			bbb:false
		}
	},
	components:{
		waitPay:waitPay,
		waitEvaluate:waitEvaluate,
		over:over,
		waitSend:waitSend,
		waitReceive:waitReceive
	},
	methods:{
		change(index){
			let lis = this.$refs.list.getElementsByTagName("li");
			for (var i = 0; i < lis.length; i++) {
				lis[i].className = "";
			}
			lis[index].className = "active";
			if(index === 0){
				for (var i = 0; i < this.arr.length; i++) {
					for(let k in this.arr[i]){
					// console.log(this.arr[i][k]);
					this.arr[i][k]=true;
					}
				}
			}
			else{
				for (var i = 0; i < this.arr.length; i++) {
					for(let k in this.arr[i]){
					// console.log(this.arr[i][k]);
					this.arr[i][k]=false;
					}
				}
				for(let k in this.arr[index-1]){
					this.arr[index-1][k]=true;
				}
			}
		}
	}
}



