
  import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

	export default {
    components:{
      Scroller,
      Spinner
    },
		data(){
			return {
				pullDown:{downContent: '下拉刷新', upContent: '释放后更新',loadingContent:'正在更新'},
				pullUp:{upContent:'上拉加载更多', downContent: '释放后加载'},
				wealthInfo:{},
				financialProductList:[],
				getBankInfos:[],
        pullupEnabled: true,
        status: {
          pullupStatus: 'default',
          pulldownStatus: 'default'
        },
        pageSize: 10,
        page: 0
			}
		},
		created(){
			// if(!this.tgt){
			// 	this.$vux.toast.show({text:"请登录"})
			// }

			let params = {
        "tgt":this.$route.query.tgt
        // "tgt":this.tgt
			};
			//请求财富总览接口
			this.$http.post(this.baseUrl + "/core/user/info/wealth/1",params).then((res)=>{
				if (res.data.message === "success") {
					this.wealthInfo = res.data.data;
			  } else {
          this.$vux.toast.show({
            type: 'text',
            text:res.data
          })
			  }
			})
			// .catch(res=>{
   //      this.$vux.toast.show({
   //        text:'服务未响应'
   //      })
   //    })
      this.getFinancialProductList('0');
		},
		methods:{
      loadMore () {
        this.page = this.page+1;
        this.getFinancialProductList(this.page,true);
        setTimeout(() => {
          this.$refs.scroller.donePullup()
        }, 50)
      },
      refresh () {
        setTimeout(() => {
          this.page = 0;
          this.getFinancialProductList(this.page);
          this.$nextTick(() => {
            setTimeout(() => {
              this.$refs.scroller.donePulldown()
              this.pullupEnabled && this.$refs.scroller.enablePullup()
            }, 10)
          })
        }, 2000)
      },
      getFinancialProductList (page,flag){
        let params = {
          // "tgt":this.tgt,
          "tgt":this.$route.query.tgt,
          page : page,
          size : 5
        };
        //理财产品持有明细接口
        this.$http.post(this.baseUrl + "/core/user/info/wealth/2",params).then((res)=>{
          if (res.data.message === "success") {
          	console.log(res.data.data);
            if (res.data.data && res.data.data.length === 0) {
              this.$refs.scroller.disablePullup() // 禁用下拉
              return;
            }
            // this.financialProductList.push(res.data.data);
            // this.financialProductList = res.data.data;
            if(this.page === 0){
              this.financialProductList = res.data.data;
            }else {
            	if(flag){
            		for (var i = 0; i < res.data.data.length; i++) {
              		this.financialProductList.push(res.data.data[i]);}
            	}
            }
          } else {
            this.$vux.toast.show({
              type: 'text',
              text:res.data
            })
          }
        })
        // .catch(res=>{
        //   this.$vux.toast.show({
        //     text:'服务器异常'
        //   })
        // })
      }
    }
	}



