// ===============================引入相关文件=============================
import Vue from 'vue';
import App from './App';
import Router from 'vue-router';
import 'assets/css/common.css';
import './share/common';
import VueResource from 'vue-resource'
// ===============================引入相关路由组件========================
import Order from 'components/myOrder/order.vue';
import Details from 'components/myOrder/details.vue';
import myWealth from 'components/myWealth/myWealth.vue';
import myBankCard from 'components/myBankCard/myBankCard.vue';
import search from 'components/myOrder/search.vue';
import orderRefund from 'components/myOrder/orderRefund.vue';
import commodityRefund from 'components/myOrder/commodityRefund.vue';
import commodityReturn from 'components/myOrder/commodityReturn.vue';
// ===============================调用相关组件============================
Vue.use(Router);
Vue.use(VueResource);

import ToastPlugin from 'vux/src/plugins/toast/index.js'
;
Vue.use(ToastPlugin);
//================================创建路由实例=============================
var router=new Router({
    routes:[
        {
            path:'/myOrder',
            component:Order
        },
        {
        	path:'/myOrder/details',
        	component:Details
        },
        {
        	path:'/myWealth',
        	component:myWealth
        },
        {
            path:'/myBankCard',
            component:myBankCard
        },
        {
            path:'/myOrder/search',
            component:search
        },
        {
            path:'/myOrder/orderRefund',
            component:orderRefund
        },
        {
            path:'/myOrder/commodityRefund',
            component:commodityRefund
        },
        {
            path:'/myOrder/commodityReturn',
            component:commodityReturn
        }
    ]
})

new Vue({
  router,
  render: h => h(App),
}).$mount('#app');

