//将时间戳转化为xxxx-xx-xx的时间格式
Vue.filter('getDate', function (time) {
  var date = new Date(time);
  var y = date.getFullYear();
  var m = date.getMonth() + 1;
  m = m < 10 ? '0' + m : m + '';
  d = d < 10 ? '0' + d : d + '';
  var d = date.getDate();
  return y + '-' + m + '-' + d;
});

Vue.prototype.formatDate = function (time) {
  var date = new Date(time);
  var y = date.getFullYear();
  var m = date.getMonth() + 1;
  m = m < 10 ? '0' + m : m + '';
  d = d < 10 ? '0' + d : d + '';
  var d = date.getDate();
  return y + '-' + m + '-' + d;
};
//简单金额过滤器 by 袁梦
Vue.filter('currency', (value, type = '$') => type + Number(value).toFixed(2));

// yyyy-MM-dd hh:mm:ss.S
Date.prototype.Format = function (fmt) {
  let o = {
    'M+': this.getMonth() + 1, //月份
    'd+': this.getDate(), //日
    'h+': this.getHours(), //小时
    'm+': this.getMinutes(), //分
    's+': this.getSeconds(), //秒
    'q+': Math.floor((this.getMonth() + 3) / 3), //季度
    S: this.getMilliseconds(), //毫秒
  };

  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length));

  for (let k in o)
    if (new RegExp('(' + k + ')').test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)));
  return fmt;
};


Vue.prototype.setTitle = function (title) {
  let body = document.getElementsByTagName('body')[0];
  document.title = title;
  let iframe = document.createElement('iframe');
  iframe.style.display = 'none';
  iframe.setAttribute('src', 'https://img6.bdstatic.com/img/image/smallpic/b.jpg');
  let d = function () {
    setTimeout(function () {
      iframe.removeEventListener('load', d);
      body.removeChild(iframe);
    }, 0);
  };

  iframe.onload = d;
  body.appendChild(iframe);
};




